<template lang="html">
  <div class="row">
    <app-single-note v-for="(note, index) in notes" @click.native="deleteNote(index)">{{ note }}</app-single-note>
  </div>
</template>

<script>
import SingleNote from './SingleNote.vue';

export default {
  props: ['notes'],
  components: {
    'app-single-note': SingleNote,
  },
  methods: {
    deleteNote(index) {
      this.$emit('deleteNote', index);
    },
  },
}
</script>

<style lang="css">
</style>



