<template lang="html">
  <section>
    <div class="row center teal-text">
      <h5 class="header col s12">Limit {{ notesCount }}/{{ maxNotes }}</h5>
    </div>
    <div class="progress">
      <div class="determinate" :style="{width: (notesCount / maxNotes * 100) + '%'}"></div>
    </div>
  </section>
</template>

<script>
export default {
  props: ['maxNotes', 'notesCount'],
}
</script>

<style lang="css">
</style>



