<template>
  <div class="">
    <app-header :maxNotes="maxNotes" :notesCount="notes.length"></app-header>
    <app-new-note :maxNotes="maxNotes" :notesLength="notes.length" @createNote="saveNote"></app-new-note>
    <div class="row center green white-text">
      <h5 class="header col s12 light">Click on note to delete it *_*</h5>
    </div>
    <app-note-grid :notes="notes" @deleteNote="deleteNote"></app-note-grid>
    <hr>
    <div class="row">
      <blockquote>This application created by Mikita Melnikau. With this application I learned about the Vue components flow.</blockquote>
    </div>
  </div>
</template>

<script>
  import NoteGrid from './components/NotesGrid.vue';
  import NewNote from './components/NewNote.vue';
  import Header from './components/Header.vue';

  export default {
    components: {
      'app-note-grid': NoteGrid,
      'app-new-note': NewNote,
      'app-header': Header,
    },
    data: function () {
      return {
        maxNotes: 10,
        notes: [
          'Angelheaded hipsters burning for the ancient heavenly connection to the starry dynamo in the machinery of night.'
        ],
      };
    },
    methods: {
      saveNote(data) {
        this.notes.push(data);
      },
      deleteNote(index) {
        this.notes.splice(index, 1);
      }
    }
  }
</script>

<style></style>



