<template lang="html">
  <section>
    <div class="row center">
      <h5 class="header col s12 light">Create a new note:</h5>
    </div>
    <div class="row">
      <div class="input-field col s6 offset-s3 m4 offset-m4">
        <textarea id="newnote" class="materialize-textarea" v-model="note"></textarea>
        <label for="newnote">New Note</label>
      </div>
      <div class="input-field col s6 offset-s3 m4 offset-m4">
        <button class="btn waves-effect waves-light" @click.prevent="createNote">Submit
          <i class="material-icons right">send</i>
        </button>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  props: ['notesLength', 'maxNotes'],
  data: function () {
    return {
      note: '',
    };
  },
  methods: {
    createNote() {
      if (this.notesLength === this.maxNotes) alert('Max notes count resived! You need to delete old one!');
      else {
        this.$emit('createNote', this.note);
        this.note = '';
      }
    },
  },
}
</script>

<style lang="css">
</style>



